* {
  background-color: black;
  color: white;
  font-family: "Poppins", sans-serif;
}

.container,
.searchContainer {
  width: 100vw;
  text-align: center;
}

.searchBox {
  width: 30vw;
  color: rgb(255, 255, 255);
  min-height: 50px;
  margin: 10vh;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );
  font-size: 1.2rem;
}

.rowSeparator,
.cryptoDisplayContainer,
.cryptoDisplay {
  color: white;
  min-width: 100vh;
  width: 100%;
}

.tableHeader {
  padding: 50vh;
}

.red {
  color: red;
}

.green {
  color: greenyellow;
}
